<template>
  <div id="Block">
      <li v-for="item in information" :key="item.content">
          <p style="line-height:15pt;padding-top:10pt;">{{"【"+item.type+"】"}}</p>
          <p>{{item.type+"人:"+item.object}}</p>
          <p v-if="item.type=='举报'">{{"举报原因:"+item.content}}</p>
          <p v-if="item.type=='反馈'">{{"反馈内容:"+item.content}}</p>
          <div v-if="item.type=='举报'" class="div">
              <input type="button" value="查看被举报的内容" style="margin-right:10pt;"  @click="to(item.type)">
              <input type="button" value="删除举报">
          </div>
          <div v-if="item.type=='反馈'" class="div">
              <input type="button" value="查看反馈的内容" style="margin-right:10pt;"  @click="to(item.type)">
              <input type="button" value="删除反馈">
          </div>
      </li>
  </div>
</template>

<script>
export default {
    name:"Block",
    data(){
        return{
            information:[
                {type:'举报',object:"用户E",content:"辣我眼睛!"},
                {type:'反馈',object:"用户F",content:"..."}
            ]
        }
    },
    methods:{
        to:function(type){
            if(type=="举报"){
                this.$router.push({path:"Handle/Complain"});
            }
            else if(type=="反馈"){
                this.$router.push({path:"Handle/Feedback"});
            }
        }
    }
}
</script>

<style scoped>
li{
    list-style: none;
    margin:5pt;
    margin-bottom:10pt;
    background-color:white;
    border-radius: 5pt;
}
.div{
    display:flex;
    justify-content: flex-end;
    margin-top:5pt;
}
input
{
    border-radius:5pt;
    border:1px solid #A49BB5;
    background-color:white;
    height:20pt;
    color:#635B73;
    margin:5pt;
}
p
{
    margin:5pt;
}
</style>